@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@800&family=Open+Sans&family=Roboto&display=swap');
*{
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
    text-decoration: none;
}
body{
    background: var(--backgroundColor);
}
.wrapper {
    display: flex;
    place-content: center;
}
.frame {
    width: 1024px;
    display: flex;
    justify-content: center;
}
.block {
    margin: 15px 0px 15px 0px;
    background: var(--blockColor);
    border-radius: 7px;
    padding: 15px 20px;
    display: flex;
    flex-wrap: nowrap;
    color: var(--textColor);
    box-shadow: 0 0 14px 0px #0000000a;
}
.imgBox {
    width: 250px;
    display: flex;
    max-height: 250px;
    overflow: hidden;
    justify-content: center;
    align-items: flex-start;
    border-radius: 7px;
}
.textBox {
    display: flex;
    flex-direction: column;
    width: fit-content;
    margin-left: 15px;
}
.img {
    height: 100%;
}
.img > img {
    height: 100%;
    border-radius: 7px;
}
.headerBox > h1 {
    font-family: "montserrat";
    font-size: 20px;
    transition: .1s;
}
.descBox {
    margin-top: 5px;
}
.descBox > p {
    font-size: 17px;
}
.block:hover > .textBox > .headerBox > h1 {
    color: var(--primaryColor);
}
.siteHeader{
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    display: flex;
}
.siteHeaderBox {
    background: var(--blockColor);
    height: 55px;
    position: fixed;
    width: 100%;
    box-shadow: 0 0 20px 0px #00000030;
}
.fm{
    margin-top: 60px;
}
.btn {
    color: var(--textColor);
    font-family: 'Montserrat';
    padding: 10px 15px;
    border-radius: 5px;
    margin: 0px 3px;
    background: var(--primaryColor);
}
.switcherBox {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
.Btn {
    padding: 5px 10px;
    background: var(--primaryColor);
    font-family: 'Montserrat';
    color: var(--textColor);
    border-radius: 7px;
    margin: 0px 5px;
    height: 20px;
}
.switcherBox > div {
    display: flex;
}
.activeBtn{
    filter: brightness(0.75);
}
.btn, .Btn, .downloadBtn {
    transition: .15s;
}
.btn:hover, .Btn:hover, .downloadBtn:hover {
    filter: brightness(0.75);
}
.gamePage {
    display: flex;
    flex-direction: column;
    width: 1024px;
}
.GamePageHeaderBox > h1 {
    font-family: 'Montserrat';
    font-size: 29px;
    margin-top: 8px;
}
.GamePageContentBox {
    margin-top: 20px;
    align-self: center;
    border-radius: 7px;
    overflow: hidden;
}
.frameSize, .frameSize > iframe{
    width: 100%;
    height: 500px;
}
.GamePageDownloadBox {
    margin-top: 45px;
    display: flex;
    justify-content: center;
}
.downloadBtn {
    background: var(--primaryColor);
    width: 325px;
    border-radius: 7px;
    padding: 15px;
    display: flex;
    flex-direction: row;
    color: var(--textColor);
    align-items: center;
}
.downloadBtnIconBox {
    display: flex;
}
.downloadBtnIconBox > span{
    font-size: 40px !important;
}
.downloadBtnTextBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 15px;
    text-align: center;
}
.downloadBtnTextBox > h2 {
    font-family: 'Montserrat';
}
.downloadBtnTextBox > span {
    font-size: 13px;
}
.settingsPage{
    flex-direction: column;
    width: 350px;
}
.settingsBox {
    display: flex;
    justify-content: center;
}
.settingsTheme {
    font-family: 'Montserrat';
    padding: 15px 40px;
    font-size: 20px;
    cursor: pointer;
    background: var(--backgroundColor);
    border-radius: 8px;
    margin-right: 10px;
}
h2.settingsName {
    font-family: 'Montserrat';
    margin-bottom: 10px;
    margin-top: 5px;
}
.settingsColor {
    width: 35px;
    height: 35px;
    background: var(--backgroundColor);
    margin: 0 5px 0 5px;
    border-radius: 7px;
    cursor: pointer;
}
.frame > .settingsBox{
    display: flex;
    justify-content: center;
}
#indianred {
    background: indianred;
}
#pink {
    background: pink;
}
#blueviolet {
    background: blueviolet;
}
#deepskyblue {
    background: deepskyblue;
}
#darkgrey {
    background: darkgrey;
}
#limegreen {
    background: limegreen;
}
#yellow {
    background: yellow;
}
#orange {
    background: orange;
}
.logo {
    color: var(--textColor);
}